import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ActivityHomeworkItemResolver } from './activity-homework.resolver';
import { ActivityHomeworkItemStudentResolver } from './activity-homework.resolver';

import { ActivityHomeworkComponent } from './activity-homework.component';
import { ActivityHomeworkItemComponent } from './activity-homework-item/activity-homework-item.component';
import { ActivityHomeworkItemStudentComponent } from './activity-homework-item-student/activity-homework-item-student.component';

const routes: Routes = [
  { path: 'list', component: ActivityHomeworkComponent},
  { path: 'list/details/:id', component: ActivityHomeworkItemComponent,
    resolve: {
      ActivityHomeworkItemResolver
    }
  },
  { path: 'list/details/:id/student/:studentid', component: ActivityHomeworkItemStudentComponent,
    resolve: {
      ActivityHomeworkItemStudentResolver
    }
  },
  { path: '', redirectTo: 'list', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ActivityHomeworkRoutingModule { }
